html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.section {
  margin-bottom: 6rem;
  padding: 0 2rem;
}

.centered-content {
  max-width: 80rem;
}

.btnLarge {
  display: block;
  width: 95%;
  padding: 1.5rem 1rem;
  outline: none;
  border: none;
  color: #fff;
  background: #000;
  border-radius: 1.5rem;
  text-align: center;
  font-weight: bolder;
  text-transform: uppercase;
  margin: 0 auto 1.5rem;
}
.btnLarge:hover, .btnLarge:focus {
  cursor: pointer;
  opacity: 0.8;
}

.btnLarge--yellow {
  background: #E08709;
}

.btnLarge--red {
  background: #bf4444;
}

.btnLarge--blue {
  background: rgba(29, 134, 166, 0.7490196078);
}

.btnSmall {
  display: inline-block;
  width: fit-content;
  outline: none;
  border: none;
  padding: 1.5rem 1rem;
  color: #fff;
  background: #000;
  border-radius: 1.5rem;
  text-align: center;
  font-weight: bolder;
  text-transform: uppercase;
}
.btnSmall:hover, .btnSmall:focus {
  cursor: pointer;
  opacity: 0.8;
}

.btnSmall--green {
  background: #71B100;
}

.btnSmall--yellow {
  background: #E08709;
}

.dark-mode {
  background: #1a1a1a;
  color: #FFFFFF;
}
.dark-mode .card, .dark-mode .blog-entry, .dark-mode .form {
  background-color: #262626;
}
.dark-mode .form {
  color: #FFFFFF;
}
.dark-mode .form legend, .dark-mode .form p {
  color: #FFFFFF;
}
.dark-mode .form fieldset, .dark-mode .form input:not([type=submit]), .dark-mode .form textarea, .dark-mode .form select {
  color: #FFFFFF;
  border-color: #333333;
  background-color: #262626;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5, .dark-mode h6 {
  color: #e1e1e1;
}
.dark-mode .card__icons img, .dark-mode .characteristics__icons img {
  filter: invert(100%);
}
.dark-mode .testimonial {
  background-color: #406500;
}

.webp .header--home {
  background-image: url(../img/header.webp);
}

.no-webp .header--home {
  background-image: url(../img/header.jpg);
}

.header {
  background: #333333;
  padding: 1rem 0 3rem;
  margin-bottom: 5rem;
}
.header .header-title {
  color: #FFFFFF;
  text-align: left;
  text-transform: capitalize;
  padding: 0 2rem;
  max-width: 70%;
  font-weight: bold;
  line-height: 2;
}

.header--home {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header--home {
    min-height: 500px;
  }
}
@media (min-width: 768px) {
  .header--home .header-container {
    min-height: 500px;
  }
}

@media (min-width: 768px) {
  .header-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.footer {
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer .navigation {
    width: 50%;
    flex-wrap: wrap;
    justify-content: flex-start;
    row-gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 0;
}

.bar {
  display: flex;
  flex-direction: column;
  padding: 2rem 3rem 0rem;
  row-gap: 4rem;
}
.bar img {
  width: 250px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.bar .dark-mode-btn {
  margin-bottom: 3rem;
  width: 2.5rem;
  filter: invert(100%);
}
.bar .dark-mode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .bar .dark-mode-btn {
    display: inline-block;
    margin-bottom: 1rem;
    margin-top: -2rem;
    margin-right: -3rem;
  }
}

.right {
  text-align: unset;
}
@media (min-width: 768px) {
  .right {
    text-align: right;
  }
}

.navigation a {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-transform: capitalize;
}
.navigation a:hover {
  color: #E08709;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    justify-content: space-between;
    column-gap: 3rem;
  }
}

.bar .navigation {
  overflow: hidden;
  height: 0;
  transition: all 600ms ease-in-out;
}
.bar .navigation.show {
  height: 18rem;
}
@media (min-width: 768px) {
  .bar .navigation.show {
    height: auto;
  }
}
@media (min-width: 768px) {
  .bar .navigation {
    height: auto;
  }
}
.bar .navigation a {
  transform: translateY(-40rem);
  transition: transform 600ms ease-in-out;
}
@media (min-width: 768px) {
  .bar .navigation a {
    transform: translateY(0);
  }
}
.bar .show a {
  transform: translateY(0);
}
.bar .burguer-icon {
  transform: rotate(0);
  transition: transform 400ms ease-in-out;
}
.bar .rotate {
  transform: rotate(90deg);
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
  transform: rotate(0deg);
}
.mobile-menu img.flip {
  transition: transform 500ms ease-in-out;
  transform: rotate(90deg);
}

@media (min-width: 768px) {
  .aboutus-icons {
    padding: 0 4rem;
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 12rem;
  width: auto;
  margin: 0 auto;
  filter: drop-shadow(0px 2px 2px #333333);
}
.icon h3 {
  text-transform: uppercase;
}

.characteristics__icons {
  width: min(50rem, 60%);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  column-gap: 2rem;
  list-style: none;
  padding: 0;
  margin: 0;
}
.characteristics__icons li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  column-gap: 2rem;
}
.characteristics__icons li img {
  flex: 3;
}
.characteristics__icons li p {
  flex: 1;
}

@media (min-width: 768px) {
  .ads-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 4rem;
  }
}
@media (min-width: 1024px) {
  .ads-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  background-color: #f8f8f8;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2156862745);
  margin-bottom: 3rem;
  padding-bottom: 2rem;
}
.card picture {
  margin-bottom: 1rem;
}
.card picture c img {
  height: 28rem;
  object-fit: cover;
}
.card .card__details {
  margin: 0 1rem;
}
.card .card__title,
.card .card__description {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}
.card .card__title {
  text-wrap: balance;
  text-transform: capitalize;
  font-size: 2.2rem;
  margin: 0;
  -webkit-line-clamp: 2;
  /* Número de líneas visibles antes del truncamiento */
  line-clamp: 2;
}
@media (min-width: 1024px) {
  .card .card__title {
    height: 7.5rem;
  }
}
.card .card__description {
  margin-top: 1rem;
  -webkit-line-clamp: 4;
  /* Número de líneas visibles antes del truncamiento */
  line-clamp: 4;
}
@media (min-width: 1024px) {
  .card .card__description {
    height: 15rem;
  }
}
.card .card__icons {
  list-style: none;
  display: flex;
  padding: 0 1rem;
  justify-content: space-between;
  padding: 0;
  justify-content: space-around;
  margin-bottom: 5rem;
}
.card .card__icons li {
  display: flex;
  padding: 0 1rem;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
  padding: 0;
  font-weight: bold;
}

.align-right {
  text-align: right;
  margin: 4rem 0 8rem;
}

.card__price {
  color: #71B100;
  text-decoration: underline;
  font-size: 2.5rem;
}
.card__price .dollar {
  font-weight: bolder;
}

.webp .contact-img {
  background-image: url(../img/encuentra.webp);
  background-position: center;
  background-repeat: repeat;
  background-size: cover;
}

.no-webp .contact-img {
  background-image: url(../img/encuentra.jpg);
}

.contact-img {
  min-height: 60vh;
  text-align: center;
  padding: 8rem 3rem 5rem;
  margin-bottom: 8rem;
}
.contact-img h2,
.contact-img p {
  margin-bottom: 4rem;
  color: #FFFFFF;
}
.contact-img h2 {
  font-weight: bold;
  font-size: 4rem;
}

@media (min-width: 768px) {
  .blog-test-section {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: 2fr 1fr;
  }
}

.blog-entry {
  margin-bottom: 6rem;
  background: #fbfbfb;
  border-radius: 1rem;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2156862745);
}
@media (min-width: 1024px) {
  .blog-entry {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    column-gap: 2rem;
    height: 28rem;
  }
  .blog-entry .image {
    flex: 2;
    height: 100%;
  }
  .blog-entry .image picture,
  .blog-entry .image img {
    height: 100%;
  }
  .blog-entry .image img {
    object-fit: cover;
  }
  .blog-entry .content {
    flex: 3;
  }
}
.blog-entry .image {
  border-radius: 8px;
  overflow: hidden;
}

.content {
  padding: 1rem;
  height: 100%;
}
.content h4,
.content p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  margin: 0;
}
.content h4 {
  -webkit-line-clamp: 2;
  /* Número de líneas visibles antes del truncamiento */
  line-clamp: 2;
}
.content p {
  margin: 1rem 0;
  padding-right: 2rem;
  -webkit-line-clamp: 4;
  /* Número de líneas visibles antes del truncamiento */
  line-clamp: 4;
}
.content a {
  text-decoration: none;
  color: inherit;
}

.text-yellow {
  color: #E08709;
}

h4 {
  position: relative;
}
h4::after {
  content: "";
  position: absolute;
  background: #71B100;
  height: 0.4rem;
  width: 70%;
  left: 0;
  bottom: 0;
  border-radius: 1rem;
  box-shadow: 0px 1px 6px -1px #E08709;
}

.testimonial {
  background: #71B100;
  color: #FFFFFF;
  border-radius: 1rem;
  padding: 5rem 3rem 4rem 8rem;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2156862745);
  margin-bottom: 4rem;
}
.testimonial blockquote {
  position: relative;
  margin: 0 0 2rem;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.2;
}
.testimonial blockquote::before {
  content: "";
  position: absolute;
  height: 5rem;
  width: 5rem;
  left: -5rem;
  top: -2rem;
  background-image: url(../img/comilla.svg);
}
.testimonial p {
  text-align: right;
}

.form {
  margin: 0 auto 4rem;
  background-color: #FFFFFF;
  max-width: 65rem;
  color: 900;
  border-radius: 1rem;
  padding: 1rem 1.5rem;
}
.form fieldset {
  margin-bottom: 3rem;
  border-radius: 1rem;
  padding: 2rem;
  border: 4px solid #e1e1e1;
}
.form legend, .form p {
  color: #333333;
}
.form legend {
  font-size: 2.2rem;
  text-transform: capitalize;
  text-align: center;
  margin: 0 auto;
}
.form label {
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.form p {
  font-size: 2rem;
  margin: 2rem 0;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  display: block;
  width: 100%;
  padding: 1.5rem 1rem;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  border-radius: 1rem;
  border: 4px solid #e1e1e1;
  background-color: inherit;
}
.form textarea {
  min-width: 100%;
  max-width: 100%;
  min-height: 15rem;
  max-height: 15rem;
  resize: none;
}
.form .contact-method label, .form .contact-method input[type=radio] {
  display: inline-block;
}
.form .contact-method label {
  margin-right: 1rem;
}
.form .contact-method input[type=radio] {
  width: auto;
  margin-right: 3rem;
}
.form .form-img--small {
  max-width: 35rem;
  margin: 0 auto 4rem;
}

.aboutus-content {
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .aboutus-content {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.aboutus-content .img {
  height: fit-content;
  align-self: center;
}
.aboutus-content picture {
  border-radius: 1rem;
  overflow: hidden;
}
.aboutus-content .description blockquote {
  font-size: 3rem;
  font-weight: bold;
  margin: 0 0 3rem;
}

.alert {
  padding: 2rem;
  color: #FFFFFF;
  background-color: transparent;
  font-weight: bold;
  margin: 2rem auto;
  border-radius: 1rem;
}
.alert.error {
  background: #bf4444;
}
.alert.success {
  background: #71B100;
}

.table-container {
  overflow: scroll;
  padding: 1rem;
  max-width: 100rem;
  margin: 0 auto;
}

table {
  border: 4px #bdafaf solid;
  border-radius: 1rem;
}

table.properties {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background: rgba(29, 134, 166, 0.7490196078);
}
table.properties thead th {
  color: #FFFFFF;
  padding: 2rem 0.5rem;
}
@media (min-width: 768px) {
  table.properties thead th {
    padding: 3rem;
  }
}
table.properties tbody td {
  text-align: center;
  padding: 1.5rem 0.5rem;
}
table.properties tbody .table-img {
  width: 10rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  table.properties tbody .table-img {
    width: 20rem;
  }
}
table.properties tbody input[type=submit], table.properties tbody a {
  max-width: 10rem;
  font-size: 1rem;
}